@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*,
*::before,
*::after {
    box-sizing: border-box;
}

:root {
    --clr-primary: rgb(220, 116, 88);
    --clr-secondary: rgb(244, 232, 210);
    --clr-accent: #776f62;

    --ff-primary: "Source Code Pro", "Lucida Console", monospace;
    --ff-secondary: "Ubuntu", Arial, Helvetica;
    --fs: 1.125rem;

    --bs: 0.25em 0.25em 0.75em rgba(0, 0, 0, .25), 0.125em 0.125em 0.25em rgba(0, 0, 0, .15);
}

body {
    margin: 0;
    line-height: 1.6;
}

.content {
    height: 100vh;
    display: grid;
    grid-template-rows: auto 1fr;
}

.top {
    top: 0;
    position: relative;
    height: 100%;
    background-color: rgb(220, 116, 88);
    background-color: var(--clr-primary);
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    box-shadow: 0.25em 0.25em 0.75em rgba(0, 0, 0, .25), 0.125em 0.125em 0.25em rgba(0, 0, 0, .15);
    box-shadow: var(--bs);
}

h1 {
    width: 100%;
    margin: 0;
    font-size: 2rem;
    color: rgb(244, 232, 210);
    color: var(--clr-secondary);
    font-family: "Source Code Pro", "Lucida Console", monospace;
    font-family: var(--ff-primary);
}

.title {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
}

.algorithm {
    padding: 1rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.algorithm--title {
    margin-right: 5px;
    color: rgb(244, 232, 210);
    color: var(--clr-secondary);
    font-family: "Source Code Pro", "Lucida Console", monospace;
    font-family: var(--ff-primary);
    font-size: 1.125rem;
    font-size: var(--fs);
}

.algorithm__select {
    z-index: 1001;
}

.settings {
    padding: 1rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.settings--input {
    margin: 5px;
    width: 150px;
}

.settings--input__title {
    color: rgb(244, 232, 210);
    color: var(--clr-secondary);
    font-family: "Source Code Pro", "Lucida Console", monospace;
    font-family: var(--ff-primary);
    font-size: 1.125rem;
    font-size: var(--fs);
}

.settings--input__label {
    color: rgb(244, 232, 210);
    color: var(--clr-secondary);
    font-family: "Source Code Pro", "Lucida Console", monospace;
    font-family: var(--ff-primary);
    font-size: 1.125rem;
    font-size: var(--fs);
}

.settings--input__slider {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    width: 100%;
    height: 10px;
    background-color: #776f62;
    outline: none;
    border-radius: 5px;
}

.settings--input__slider::-webkit-slider-thumb {
    -webkit-appearance: none;
            appearance: none;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: rgb(244, 232, 210);
    background-color: var(--clr-secondary);
    cursor: pointer;
}

.nav {
    margin: 0;
    padding: 1rem;
    display: flex;
    flex-grow: 2;
    justify-content: center;
    align-items: center;
    grid-column: span 3;
}

.nav__btn {
    height: 60%;
    min-height: 3rem;
    width: 100px;

    border: none;
    outline: none;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;

    font-size: 1.5rem;
    font-family: "Ubuntu", Arial, Helvetica;
    font-family: var(--ff-secondary);

    background-color: rgb(244, 232, 210);

    background-color: var(--clr-secondary);
    color: #776f62;
    color: var(--clr-accent);
    transition: width .2s ease-in-out, box-shadow .1s ease-in-out;
}

.nav__btn:hover {
    background-color: #776f62;
    background-color: var(--clr-accent);
    color: rgb(244, 232, 210);
    color: var(--clr-secondary);
    width: 140px;
}

.nav__btn:active {
    box-shadow: inset 0px 0px 10px rgba(0, 0, 0, 0.5);
}

.body {
    height: 100%;
    width: 100vw;
    min-width: 1100px;

    display: flex;
    justify-content: center;
    align-items: center;
}

@media(min-width: 1420px) {
    .top {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
    }
}

@media(min-width: 1470px) {
    .top {
        display: flex;
        flex-wrap: wrap;
    }
}

@media(min-width: 1690px) {
    .nav {
        justify-content: flex-end;
    }
}
.visualizer {
    background-color: var(--clr-secondary);
    width: 80vw;
    height: 80vh;
    min-height: 735px;
    min-width: 1024px;

    align-self: center;

    display: flex;
    justify-content: center;
    align-items: flex-end;

    border-radius: 10px;
}
.bar {
    background: rgba(92, 92, 92, 1);
    margin-bottom: 100px;
}

.bar.current-min {
    background: rgba(199, 95, 95, 1);
}

.bar.current {
    background: rgba(101, 187, 147, 1);
}

.bar.sorted {
    background: rgba(47, 47, 47, 1);
}

.tooltip .tooltip--text {
    visibility: hidden;
    opacity: 0;
    position: absolute;
    margin-left: 10px;
    top: -32px;
    left: -13px;

    padding: 0 5px;

    background-color: rgb(73, 73, 73);
    color: antiquewhite;
    transform: rotate(-45deg);
    transition: opacity .2s ease-in-out;
}

.tooltip {
    position: relative;
    display: inline-block;
}

.tooltip:hover .tooltip--text {
    visibility: visible;
    z-index: 1000;
    opacity: 1;
}
.algorithm-select__container {
    display: inline-block;
    min-width: 200px;
    text-align: center;
    position: relative;
    width: 100%;
}

.selected-text,
li {
    padding: 6px 20px;
    background-color: var(--clr-accent);
    color: var(--clr-secondary);
    width: 250px;

    font-family: var(--ff-primary);
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.selected-text {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.arrow {
    position: relative;
    height: 15px;
    width: 15px;
}

.arrow::before,
.arrow::after {
    content: "";
    position: absolute;
    bottom: 0px;
    width: 0.15rem;
    height: 100%;
    transition: all .5s;
}

.arrow::before {
    left: -5px;
    transform: rotate(-45deg);
    background-color: var(--clr-secondary);
}

.arrow::after {
    left: 5px;
    transform: rotate(45deg);
    background-color: var(--clr-secondary);
}

.selected-text.active .arrow::before {
    left: -5px;
    transform: rotate(45deg);
}

.selected-text.active .arrow::after {
    left: 5px;
    transform: rotate(-45deg);
}

.algorithm-select__list {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    transition: all .25s ease-in-out;
}

.algorithm-select__list.active {
    opacity: 1;
    visibility: visible;
}

ul {
    margin: 0;
    padding: 0;
    text-align: center;
}

li {
    list-style-type: none;
    cursor: pointer;
}

li:hover {
    background-color: #ccc;
    color: var(--clr-accent);
}
